@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  background-color: black;
  color: white;
  margin: 0;
  padding: 15px;
}
html {
  background-color: rgb(245, 245, 245);
}

.input-label {
  padding: 1em;
  text-align: center;
}
.input-label label {
  display: block;
}
input {
  padding: 5px;
}

.flex-container {
  display: flex;
  justify-content: space-around;
  height: 300;
}

body {
  min-height: 10000px;
}
/*---------TABLE ---------- */
table {
  margin: 2em auto 0 auto;
  border-collapse: collapse;
}

table thead {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  background-color: black;
  color: white;
}
table thead tr th {
  padding: 10px;
  border: 1px solid black;
}

table tbody tr td {
  text-align: center;
  border: 1px solid black;
  padding: 10px;
}

table tbody tr:nth-child(odd) {
  background-color: rgb(228, 228, 228);
}
div.flex-tooltip {
  display: flex;
  justify-content: center;
  justify-items: center;
}
div.tooltip-container {
  display: flex;
  flex-direction: column;
  position: relative;
}

div.tooltip-content {
  visibility: hidden;
  position: absolute;
  background-color: black;
  color: white;
  top: -65px;
  width: 150px;
  left: -72px;
  padding: 10px;
  font-size: small;
}
div.tooltip-content::after {
  content: " ";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

div.tooltip img {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  margin-left: 5px;
  margin-top: 2px;
}

div.tooltip-container:hover {
  cursor: pointer;
}

div.tooltip-container:hover div.tooltip-content {
  visibility: visible;
}

.helper-inputs-container {
  background-color: #303030;
  padding: 15px;
}

.helper-input {
  background-color: white;
  border-radius: 15px;
  padding: 25px;
}

.summary {
  background-color: #303030;
  padding: 25px;
  padding-top: 50px;
}

.summary div {
  background-color: black;
  border-radius: 15px;
  padding: 25px;
  font-size: 1.5em;
  color: white;
}

button.download {
  display: block;
  max-width: 500px;
  font-size: 1em;
  margin: 25px auto;
  background-color: black;
  color: white;
  padding: 10px;
  border-radius: 10px;
  border: none;
  transition: all 500ms;
}

button.download:hover {
  cursor: pointer;
  color: black;
  background-color: lightgray;
}

